body {
  font: $base-regular-font;
  color: $base-gray;
}

.wrapper {
  max-width: $wrapper-width;
  margin: auto;
}

.container {
  width: 100%;
  margin: auto;
}

.row-no-margin {
  margin-right: 0;
  margin-left: 0;
}

.full-width {
  width: 100%;
}

a {
  text-decoration: none;
  color: $base-gray;
  &:hover,
  &:focus {
    text-decoration: none;
    color: $base-blue;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

*,
*:after,
*:before {
  margin: 0;
  padding: 0;

  @include border-box;
}

img {
  width: auto;
  max-width: 100%;
  height: auto;
}